@use 'sass:map';
@use 'abstracts/variables';
@use 'abstracts/functions';
@use 'abstracts/mixins';

// IE11 전용 css hack
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	body {
		.wrapper {
			// header 중앙정렬 여백 수정
			.header_wrapper {
				.header_inner {
					.gnb_search_wrap {
						.gnb_search_box {
							.form_sel {
								.ui-selectmenu-text {
									//padding: 15px 16px 15px 0;
								}
							}
						}

						.user_menu_list {
							.user_menu_item {
								.user_menu_link {
									.cnt {
										padding-top: 1px;
									}
								}
							}
						}
					}

					.gnb_wrap {
						.gnb_list {
							padding-bottom: 2px;

							.gnb_item {
								.gnb_link {
									&.picks {
										img {
											@include mixins.transform(translateY(0));
										}
									}
								}

								+ .gnb_item {
									&:before {
										margin-top: 12px;
									}
								}
							}
						}

						.gnb_sub_list {
							padding-bottom: 2px;

							.gnb_sub_item {
								+ .gnb_sub_item {
									&:before {
										margin-top: 11px;
									}
								}
							}
						}
					}
				}

				.service_mall_wrap {
					.service_mall_inner {
						.service_mall_box {
							.service_mall_list {
								.service_mall_item {
									&.service_more {
										.btn_service_more {
											&:after {
												margin-top: 8px;
											}
										}
									}
								}
							}
						}

						.customer_service_box {
							.customer_service_list {
								.customer_service_item {
									.btn_member_benefit {
										&:after {
											margin-top: 8px;
										}
									}
								}
							}
						}
					}
				}

				&.sps {
					&.sps-abv {
						position: static;
					}

					&.sps-blw {
						.header_inner {
							.gnb_search_wrap {
								.gnb_search_box {
									.form_sel {
										.ui-selectmenu-text {
											//padding: 13px 16px 15px 0;
										}
									}
								}
							}
						}
					}
				}
			}

			.container_wrapper {

				// breadcrumb 중앙정렬 여백 수정
				.breadcrumb_wrap {
					.breadcrumb_inner {
						.breadcrumb_list {
							.breadcrumb_item {
								.btn_sub_depth {
									&:after {
										margin: 2px 0 0 3px;
									}
								}
							}
						}
					}
				}

				// toast_message 중앙정렬 여백 수정
				.toast_wrap {

					.toast_message {
						padding: 0 30px;
					}
				}

				// 버튼 - 텍스트 링크
				.btn_text_link {
					.ico_arw {
						margin-top: 7px;
					}
				}

				// 버튼 - 컨텐츠 하단 더보기 아이콘 중앙정렬 여백 수정
				.btn_more_cont {

					.ico_arw {
						margin-top: 1px;
					}
				}

				// 버튼 - 상세정보 더보기 아이콘 중앙정렬 여백 수정
				.btn_more_detail {

					.ico_plus {
						margin-top: 2px;
					}
				}

				// 버튼 - 더보기(자세히보기) 아이콘 중앙정렬 여백 수정
				.btn_more_view {

					.ico_arw {
						margin-top: 7px;
					}
				}

				// 버튼 - 초기화, 삭제 아이콘 중앙정렬 여백 수정
				.btn_reset {

					.ico_reset {
						margin-top: 4px;
					}
				}

				.btn_text_delete {

					.ico_delete {
						margin-top: 2px;
					}
				}

				.btn_alarm {
					padding-bottom: 2px;

					.ico_alarm {
						margin-top: 3px !important;
					}

				}

				// form - 유효성 검사 실패시 경고 아이콘 중앙정렬 여백 수정
				.valid_check {
					&.fail {
						.valid_desc {
							&:before {
								top: 4px;

							}
						}
					}
				}

				.form_filter_box {
					.form_chk_filter {
						input {
							+ label {
								line-height: 42px;
							}
						}
					}
				}

				// select arw 타입 아이콘 중앙정렬 여백 수정
				.form_sel {
					// select - arw type button
					&.type_arw {
						.ui-selectmenu-button {
							.ui-selectmenu-icon {
								top: 1px;
							}
						}

					}
				}

				// badge
				.badge_md {
					padding: 1px 5px 2px;
				}

				.badge_sm {
					&.badge_pill {
						padding: 2px 7px 0;
					}
				}

				.badge_best {
					padding: 2px 5px 2px;
				}

				// table
				.tbl_col_wrap {
					// 상품상세 상단 전용 table
					&.type_prod_detail {
						.tbl_col {
							thead {
								tr {
									th {
										padding: 9px 10px 9px;
									}
								}
							}

							tbody {
								tr {
									td {
										padding: 14px 10px 13px;
									}
								}
							}
						}
					}
				}

				.price_value_box {
					.unit {
						margin-top: -2px;
					}
				}

				// 상품 공통
				.review_quotes_text {
					&:before {
						top: 5px;
					}

					&.font_size_xxs {
						&:before {
							top: 3px;
						}
					}
				}

				.review_klover_text {
					&:before {
						top: 4px;
					}

					&.font_size_xxs {
						&:before {
							top: 3px;
						}
					}
				}

				.review_klover_box {
					.review_desc {
						margin-top: 1px;
					}
				}

				.review_summary_wrap {
					.rating-container {
						&.rating-sm {
							margin-top: 4px;
						}

						& + .gap {
							margin-left: 5px;
						}
					}

					&.type_sm {
						.rating-container {
							&.rating-sm {
								margin-top: 4px;
							}
						}

						.gap {
							& + .review_quotes_text {
								margin-top: 0;
							}
						}
					}
				}

				.prod_detail_header {
					.header_contents_inner {
						// 상품 상세 상단 view - title
						.prod_detail_title_wrap {
							.prod_title_area {
								.prod_title_box {
									.auto_overflow_footer {
										bottom: -4px;
									}
								}
							}

							.prod_desc_area {
								.prod_desc_box {
									.gap {
										margin: 7px 10px 0;
									}
								}
							}
						}

						// 상품 상세 상단 view - 컨텐츠 영역
						.prod_detail_view_wrap {
							.prod_detail_type_area {
								.prod_type_list_wrap {
									.btn_prod_type {
										padding: 4px 10px 7px;
									}
								}
							}

							.prod_detail_view_area {
								.col_prod_info {
									.prod_author_box {
										.auto_overflow_footer {
											.btn_more_detail {
												margin-top: 1px;
											}
										}
									}

									.prod_price_wrap {
										.prod_price_box {
											.prod_price {
												.price {
													margin-top: -2px;
												}

												.sale_price {
													margin-bottom: 4px;
												}
											}
										}
									}

									.prod_guide_wrap {
										.prod_guide_box {
											.prod_guide_row {
												.prod_guide_cont {
													.point_text {
														.tooltip_wrap {
															.tooltip_info_list {
																.tooltip_info_item {
																	.title_wrap {
																		.btn_info_popup {
																			margin: 2px 0 0 3px;
																		}
																	}
																}
															}
														}

														.benefit_link_box {
															.btn_link {
																&:after {
																	top: 4px;
																}
															}
														}
													}

													.delivery_guide_text {
														.tooltip_wrap {
															margin: 3px 0 0 3px;
														}

														.btn_info_popup {
															margin: 3px 0 0 3px !important;
														}
													}
												}
											}
										}
									}

									&.thumb {
										.prod_thumb_swiper_wrap {
											&.active {
												.swiper_control_floating_box {
													.btn_img_zoom {
														.text {
															padding-top: 0;

															&:before {
																margin: 13px 5px 0 0;
															}
														}
													}
												}
											}
										}
									}

								}
							}
						}
					}
				}

				.prod_detail_contents {
					.prod_detail_contents_inner {
						.product_detail_area {
							.btn_alarm {
								margin-top: 2px;
							}
						}

						.product_package {
							.title_prod_package {
								.info_price {
									.price_amount_before {
										&:before {
											top: 11px !important;
										}
									}
								}
							}
						}

						.klover_review_wrap {
							.title_wrap {
								.tooltip_wrap {
									margin-top: 5px;
								}
							}
						}

						.product_keyword_pick {
							.title_wrap {
								.tooltip_wrap {
									margin-top: 3px;
								}
							}
						}

						.killing_part {
							.tooltip_wrap {
								margin-top: 5px
							}
						}
					}
				}
			}

			.cart_body {
				.tag_wrap.type_line_white {
					.tag {
						padding: 7px 5px 9px 6px !important;

						&.has_data {
							padding: 7px 16px 9px !important;
						}
					}
				}
			}

			// footer 중앙정렬 여백 수정
			.footer_wrapper {
				.footer_body {
					.footer_contents_wrap {
						.footer_contents_left {
							.footer_menu_box {
								.footer_menu_list {
									.footer_menu_item {
										+ .footer_menu_item {
											&:before {
												margin-top: 6px;
											}
										}
									}
								}
							}

							.footer_info_box {
								.gap {
									margin-top: 10px;
								}
							}
						}
					}
				}
			}

			&.book_cast {
				.bookcast_main_content_area {
					&.login_induce {
						.contents_inner {
							.btn_wrap {
								.btn_login_induce {
									padding: 7px 5px 0;
								}
							}
						}
					}
				}
			}

			&.member_info {
				.container_wrapper {
					.my_grade_info_wrap {
						.my_grade_info_area {
							.grade_info_contents {
								.right_area {
									.grade_state_info_list {
										.state_info_title {
											.tooltip_wrap {
												margin: 0 0 0 3px;
											}

											.btn_info_popup {
												margin: 3px 0 0 3px;
											}
										}
									}
								}
							}
						}
					}

					.benefit_grade_wrap {
						.extra_rate_text {
							.ico_point_plus {
								margin-top: 4px;
							}
						}
					}

					.step_highlight_list {
						.step_item {
							.step_text_box {
								.step_num {
									margin-top: 2px;
								}
							}
						}
					}
				}
			}
		}

		.btn_xxs {
			padding: 1px 5px 2px;
		}

		.btn_arw_link {
			&:after {
				margin: 4px 0 0 6px;
			}
		}

		.bul_item_dot {
			&:before {
				top: 9px !important;
			}

			// font-size: 12px
			&.font_size_min {
				&:before {
					top: 8px !important;
				}
			}
		}

		.btn_more_body {
			.ico_arw {
				margin-top: 1px;
			}
		}

		// tab
		.tab_wrap {
			&.type_line_round {
				> .tab_list_wrap {
					> .tabs {
						.tab_item {
							.tab_link {
								overflow: visible;
							}
						}
					}
				}
			}
		}

		// tag
		.tag_wrap {
			.tag {
				padding: 7px 16px 9px;
			}

			&.size_sm {
				.tab {
					padding: 1px 10px 3px;
				}
			}
		}

		.form_wrap {
			.form_box {
				.form_cont {
					.form_prefix_box {
						.form_ip {
							line-height: 1;
						}
					}
				}

				&.type_row {
					.form_title {
						.has_ip {
							padding-top: 10px;
						}
					}
				}
			}
		}

		// radio/check
		.form_rdo, .form_chk {
			input {
				+ label {
					padding: 0 0 0 27px;
				}
			}
		}

		//툴팁 아이콘
		.tooltip_wrap {
			&.type_sm {

				.btn_tooltip {
					height: 19px;
					padding-bottom: 3px;

					.ico_info {
						margin-top: 3px;
					}
				}
			}
		}

		// 숙박정보 & 장소안내 아이콘
		.ico_room_period,
		.ico_room_type,
		.ico_place_pin,
		.ico_place_tel {
			margin-top: 2px;
		}

		// 뱃지
		.badge_sm {
			line-height: 17px;
		}

		// 토글
		.form_toggle_box {
			.toggle_text {
				padding-top: 1px;
			}
		}

		.byte_check_wrap {
			.form_textarea {
				// IE에서 byte_check_wrap 내부 textarea placeholder 안보이는 현상 해결
				font-size: variables.$fontSizeXs;
			}
		}

		// 팝업
		.dialog_wrapper {

			.ui-dialog {
				.dialog_wrap {
					// 주소찾기 팝업
					&.dialog_address {
						&.ui-dialog-content {
							.dialog_contents {
								// custom scroll 높이값 재계산
								.custom_scroll_wrap {
									.simplebar-content-wrapper {
										min-height: 100%;
										height: 100% !important;
									}

									.address_contents_inner {
										.address_success_result {
											flex: 1 0 auto;
										}
									}

									.address_success_result {
										.address_success_wrap {
											.address_success_list {
												.address_success_item {
													.success_item_text {
														.road_name_wrap,
														.land_number_wrap {
															.badge_md {
																line-height: 18px;
															}

															.btn_road_name,
															.btn_land_number {
																padding-top: 0;

																.text {

																}
															}
														}
													}
												}
											}
										}
									}
								}

								.member_address_wrap {
									.member_address_inner {
										.member_address {
											.member_name {
												line-height: 21px;
											}
										}
									}
								}
							}
						}
					}

					.title_wrap {
						.count {
							margin: 1px 0 0 4px;
						}
					}
				}

				&.dialog_main {
					.dialog_wrap {
						&.ui-dialog-content {
							.dialog_main_btn_wrap {
								.btn_main_pop {
									.text {
										padding-top: 1px;
									}
								}
							}
						}
					}
				}
			}
		}

		//상품목록
		.showcase_person_wrap {
			.showcase_person_row {
				.person_info_area {
					.person_profile_box {
						.profile_info {
							.person_name {
								.btn_wish_icon {
									top: 0;
								}
							}
						}
					}
				}
			}
		}

		.prod_gift_text {
			&:before {
				margin: 4px 8px 0 0;
			}
		}

		.showcase_template_wrap {
			.few_related_book_wrap {
				.book_info_swiper_wrap {
					.book_info_box {
						.prod_price {
							margin-top: 1px;

							.gap {
								margin-top: -2px;

								& + .point {
									padding: 3px 0 0;
								}
							}
						}
					}
				}
			}
		}

		.step_pill_list {
			.step_item {
				.step_text_box {
					.step_text {
						margin-top: 1px;
					}
				}
			}
		}

		.cart_top_wrap {
			.order_type_box {
				.order_sort_wrap {
					.btn_order_sort {
						overflow: visible;
						padding: 9px 23px 11px;
					}
				}
			}
		}

		.btn_attach {
			&.size_md label {
				padding-top: 9px;
			}
		}

		.input_price_box {
			.form_ip {
				//padding-bottom: 4px;
			}
			.unit {
				top: 15px;
			}
		}

		.form_desc {
			.price_value {
				.price {
					.val {
						transform: translateY(2px);
					}
				}
			}

			.btn_info_popup {
				margin-top: 2px;
			}
		}

		.prod_area {
			.prod_info_box {
				.prod_cut_info {
					.text {
						margin-top: 3px;
					}
				}

				.prod_price {
					&.type_cut {
						.price_info {
							.gap {
								+ .point {
									padding-top: 4px;
								}
							}
						}
					}
				}
			}
		}

		// jQueryUI - Datepicker
		.ui-datepicker {
			.ui-datepicker-calendar {
				td {
					.ui-state-default {
						padding-left: 1px;
					}
				}
			}
		}

		// 마이룸
		.pickup_info_text {
			.gap {
				margin-top: 7px;
			}
		}
		.delivery_tracking_wrap {
			.delivery_status_box {
				.status_location {
					padding-top: 0;

					.gap {
						margin-top: 7px;
					}
				}
			}

			.delivery_tracking_list {
				.tracking_item {
					.tracking_info_list {
						.info_item {
							.status_location {
								.gap {
									margin-top: 8px;
								}
							}
						}
					}
				}
			}
		}

		.order_history_group {
			.order_history_box {
				.history_desc {
					margin-top: 1px;
				}

				&.type_title {
					.history_val {
						margin-top: 3px;
					}
					.history_desc {
						margin-top: 5px;
					}
				}
			}
		}

		.order_history_group {
			.order_history_box {
				&.type_title {
					.history_desc {
						.btn_info_popup {
							margin-top: 1px;
						}
					}
				}
			}
		}

		.payment_price_wrap {
			.payment_price_total {
				.right_box {
					.price .unit {
						padding-top: 0;
						margin-top: -1px;
					}
				}
			}

			.payment_price_list {
				.payment_price_item {
					.right_box {
						.price {
							.unit {
								padding-top: 0;
								margin-top: -1px;
							}
						}
					}
				}
			}
		}

		// 주문서
		.floating_input_item {
			.floating_guide,
			.floating_label {
				padding-top: 10px;
			}

			.form_prefix_box {
				&.country_code {
					.prefix_unit {
						line-height: 1 !important; // IE에서 드래그할 때 움직이는 이슈 처리를 위함
					}
				}
			}

			&.focus {
				.form_ip {
					padding-top: 18px;
				}
			}

			&.value {
				.form_ip {
					padding-top: 18px;
				}
			}
		}

		.benefit_summary {
			.form_chk input + label, .form_rdo input + label {
				padding-top: 0;
			}

			.price .unit {
				padding-top: 0;
			}
		}

		.point_conversion_info_wrap {
			.payment_price_wrap {
				.payment_price_list {
					.payment_price_item {
						.right_box {
							.price {
								.unit {
									padding-top: 0;
								}
							}
						}
					}
				}
			}
		}

		.title_wrap {
			.count_box {
				.unit {
					padding-top: 0;
					margin-top: -1px;
				}
			}
		}

		.use_gift_card_wrap {
			.gift_card_swiper_wrap {
				.gift_card_list {
					.gift_card_item {
						.btn_check_gift_card {
							.text {
								padding-top: 5px;
							}
						}
					}
				}
			}
		}

		// 마이룸
		.my_profile_area {
			.profile_inner {
				.profile_thumb_box {
					.badge_lg {
						.text {
							padding-bottom: 4px;
						}
					}
				}
				.profile_name_box {
					.btn_setting {
						@include mixins.transform(translateY(3px));
					}
				}
			}
		}

		.tbl_prod_wrap {
			&.type_myroom {
				.tbl_prod {
					tbody {
						tr {
							td {
								.btn_wrap.full {
									.btn_sm {
										overflow: visible;
									}
								}
							}
						}
					}
				}
			}
		}

		.title_wrap {
			.right_area {
				.prod_filter_wrap {
					.filter_area {
						.filter_bottom {
							.btn_ip {
								~.btn_ip {
									margin-left: 6px;
								}
							}
						}
					}
				}
			}

			.btn_info_popup {
				margin-top: 4px;
			}
		}

		.payment_info_wrap {
			.payment_info_area {
				.payment_total_item {
					.payment_info_box {
						.type_link {
							&:after {
								margin-top: 2px;
							}
						}
					}
				}
			}
		}

		.btn_favorite {
			&.has_text {
				background-position-y: 3px;
			}
		}

		// 전시
		.survey_status_box {
			.survey_status {
				.status {
					padding-top: 0;
				}
			}

			.right_area {
				.join_num_box {
					.join_num {
						background-position-y: center
					}
				}
			}
		}
	}
}