@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

/* 통합검색 */
.auto_complete_wrap {
	.search_content_wrap {
		display: block;
		box-sizing: border-box;

		// 검색어 입력 전 - 상단 최근 검색어
		.recommend_keyword_area {
			padding-bottom: 30px;
			margin-bottom: 30px;
			border-bottom: 1px solid variables.$colorGray04;
			box-sizing: border-box;
		}

		// 검색어 입력 전 - 하단 컨텐츠
		.keyword_contents_area {
			@include mixins.justify_width;
			align-items: stretch;
			box-sizing: border-box;

			// 지금 많이 찾는 상품 or 함께 많이 본 상품
			.keyword_prod_box {
				flex: 0 auto;
				width: 290px;
				margin: 0 52px;

				.title_wrap, .keyword_prod_swiper_wrap {
					margin: 0 10px;
				}

				.keyword_prod_swiper_wrap {
					width: calc(100% - 20px);

					.keyword_prod_swiper {
						width: 100%;
					}
				}
			}

			// 실시간 인기 검색어
			.hot_keyword_box {
				//@include mixins.justify_width(start, start, column);
				//align-items: stretch;
				flex: 0 auto;
				width: 280px;

				.hot_keyword_list_wrap {
					@include mixins.justify_width(start, start, column);
					align-items: stretch;
					flex: 1;

					.hot_keyword_list {
						display: block;
						margin-bottom: 16px;

						.hot_keyword_item {
							display: block;

							@include mixins.clearfix;

							.left_area {
								float: left;
								font-size: 0;
								width: calc(100% - 32px);

								.hot_keyword_link {
									display: inline-block;
									max-width: 100%;

									.rank {
										display: inline-block;
										flex: 0 auto;
										width: 15px;
										margin-right: 10px;
										color: variables.$textColorGray;
										@include mixins.font_size_xxs;
										font-weight: variables.$fontWeightBold;
										vertical-align: top;
									}

									.keyword {
										display: inline-block;
										width: calc(100% - 25px);
										flex: 1;
										color: variables.$textColorBlack;
										@include mixins.font_size_xxs;
										@include mixins.ellipsis(false, true, false);
										vertical-align: top;
									}

									&:hover, &:focus, &:active {
										.keyword {
											text-decoration: underline;
										}
									}
								}
							}

							.right_area {
								float: right;
							}

							& ~ .hot_keyword_item {
								margin-top: 16px;
							}

							&.top {
								.left_area {
									.rank {
										color: variables.$textColorGreen;
									}
								}
							}
						}
					}

					.hot_keyword_bottom {
						margin-top: auto;

						.info_text {
							text-align: right;
						}
					}
				}
			}

		}

		// 검색어 입력 시 - col box
		.col_left_box {
			flex-shrink: 0;
			width: 460px;
			box-sizing: border-box;
		}

		.col_right_box {
			flex-shrink: 0;
			width: 420px;
			margin-left: 54px;
			box-sizing: border-box;
		}

		// 검색어 입력 시 - 상품
		.related_product_area {
			@include mixins.justify_width;
			align-items: stretch;
			box-sizing: border-box;

			.col_left_box {}

			& ~ .related_contents_area {
				margin-top: 40px;
			}

			.related_prod_swiper_wrap {
				.prod_info_box {
					flex: 1;
				}
			}

			.btn_wrap {
				margin-bottom: 20px;
			}

			.related_prod_detail_wrap {
				display: block;
				padding-top: 41px;

				.prod_area{
					align-items: flex-start;

					.prod_info_box{
						padding-top: 20px;

						.prod_price{
							margin-top: 30px;
						}
					}
				}

				.prod_summary_box {
					display: block;
					padding: 19px 19px;
					margin-top: 20px;
					border: 1px solid variables.$colorGray04;
					border-radius: 10px;
					box-sizing: border-box;

					.info_text {
						color: variables.$textColorLightGray;
						@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 5, false);
					}
				}
			}
		}

		// 검색어 입력 시 - 하단 컨텐츠
		.related_contents_area {
			@include mixins.justify_width;
			align-items: stretch;
			box-sizing: border-box;

			.col_left_box {}

			// 데이터 없는 경우
			.no_data {
				padding: 0;
				align-items: flex-start;

				.no_data_desc {
					text-align: left;

					&:before {
						display: none;
					}
				}
			}

			// 작가
			.related_author_list_wrap {
				.related_author_list {
					.related_author_item {
						display: block;
						color: variables.$textColorBlack;
						@include mixins.font_size_xxs;

						.related_author_link {
							@include mixins.justify_width(start, start, row, true);

							&:hover, &:focus, &:active {
								text-decoration: underline;
							}

							.author {
								position: relative;
								flex: 0 0 auto;
								margin-right: 3px;
							}

							.text {
								position: relative;
								flex: 1 0 0;
								@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);

								.matched {
									color: variables.$textColorPurple;
									font-weight: variables.$fontWeightBold;
								}
							}
						}

						& ~ .related_author_item {
							margin-top: 16px;
						}
					}
				}
			}

			// 이벤트
			.related_event_swiper_wrap {
				.related_event_swiper {
					.swiper-slide {
						width: 202px;
						margin-right: 16px;

						&:last-child {
							margin-right: 0;
						}
					}

					.related_event_wrap {
						display: block;
						width: 202px;
						box-sizing: border-box;

						.related_event_link {
							display: block;
							width: 100%;

							.event_img_box {
								display: block;
								width: 100%;
								overflow: hidden;
								border-radius: 10px;

								img {
									width: 100%;
								}
							}

							.event_info_box {
								display: block;
								margin-top: 12px;

								.event_title {
									display: block;
									color: variables.$textColorBlack;
									@include mixins.font_size_xxs;
									font-weight: variables.$fontWeightMedium;
								}

								.event_period {
									display: block;
									margin-top: 5px;
									color: variables.$textColorLightGray;
									@include mixins.font_size_min;
								}
							}
						}
					}
				}
			}
		}

		.util_area {
			margin-top: 24px;
			text-align: right;
		}

		.ico_arw {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include mixins.ratio_images('../images/ico_auto_complete_arw', 'png', 10px, 10px);

		}
		.ico_search, .ico_link {
			display: inline-block;
			width: 12px;
		}
		.ico_search {
			@include mixins.ratio_images('../images/ico_auto_complete_search', 'png', 12px, 18px);
		}
		.ico_link {
			@include mixins.ratio_images('../images/ico_auto_complete_link', 'png', 12px, 18px);
		}
	}
}

// 최근 검색어
.recent_keyword_box {
	@include mixins.justify_width(start, start, column);
	align-items: stretch;
	flex: 0 auto;
	width: 260px;

	.recent_keyword_list_wrap {
		@include mixins.justify_width(start, start, column);
		align-items: stretch;
		flex: 1;

		.recent_keyword_list {
			display: block;
			margin-bottom: 15px;

			.recent_keyword_item {
				@include mixins.justify_width(center);

				.left_area {
					flex: 1;
					font-size: 0;

					.recent_keyword_link {
						display: inline-block;
						max-width: 184px;

						.keyword {
							display: inline-block;
							color: variables.$textColorBlack;
							@include mixins.font_size_xxs;
							@include mixins.ellipsis(false, true, false);
						}

						&:hover, &:focus, &:active {
							.keyword {
								text-decoration: underline;
							}
						}
					}
				}

				.right_area {
					margin-left: auto;
					font-size: 0;

					.date {
						display: inline-block;
						color: variables.$textColorLightGray;
						@include mixins.font_size_min;
						vertical-align: top;
					}

					.btn_keyword_del {
						display: inline-block;
						width: 15px;
						height: 15px;
						margin: 1px 0 0 8px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_delete', 'png', 15px, 15px);
						vertical-align: top;
					}
				}

				& ~ .recent_keyword_item {
					margin-top: 16px;
				}
			}
		}

		.no_data {
			@include mixins.justify_width(center, center, column);
			width: 100%;
			height: 333px;
			padding: 0;
			text-align: center;
			box-sizing: border-box;
		}

		.recent_keyword_bottom {
			margin-top: auto;

			.btn_wrap {
				text-align: right;

				.gap {
					display: inline-block;
					font-size: 0;
					width: 1px;
					height: 8px;
					margin: 5px 6px 0;
					background: variables.$colorGray03;
					vertical-align: top;
				}

				.save_keyword_text {
					display: inline-block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xxs;
					vertical-align: top;

					.btn_save_keyword {
						display: inline-block;
						color: variables.$textColorGray;
						@include mixins.font_size_xxs;
						font-weight: variables.$fontWeightBold;
					}
				}
			}
		}
	}
}

/* 통합검색 - 검색결과 */
.search_result_wrap {
	@include mixins.justify_width;

	.filter_area {
		flex: 0 0 170px;
		flex-shrink: 0;
		margin-top: 6px;
	}

	.result_area {
		width: calc(100% - (36px + 170px));
		margin-left: 36px;
	}

	.filter_list {

		.btn_filter_depth1, .btn_filter_depth2, .btn_filter_depth3 {
			&:not(.active) {
				+ .filter_list {
					display: none;
				}
			}
		}

		.btn_filter_depth1 {
			@include mixins.justify_width(center, between);
			width: 100%;
			text-align: left;
			@include mixins.font_size_xs;
			font-weight: variables.$fontWeightMedium;

		}

		.btn_filter_depth2, .txt_filter_depth2 {
			padding-bottom: 5px;
			@include mixins.font_size_xxs;

			&.active {
				color: variables.$textColorPurple;
				font-weight: variables.$fontWeightBold;
			}
		}

		.btn_filter_depth2 {
			padding-top: 5px;
		}

		.btn_filter_depth3, .txt_filter_depth3 {
			color: variables.$textColorGray;
			padding: 3px 0;
			@include mixins.font_size_xxs;

			&.active {
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;

				&:before {
					content: '선택됨';
					display: inline-block;
					width: 5px;
					height: 19px;
					margin-right: 5px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/arw_filter_depth3_active', 'png', 5px, 9px);
					font-size: 0;
					vertical-align: top;
				}
			}
		}

		.btn_filter_depth4 {

		}

		.txt_filter_depth2, .txt_filter_depth3 {
			display: block;
		}

		.form_chk, .form_rdo {
			padding: 5px 0;
		}

		.item_depth1.active {
			.filter_list.depth2 {
				margin: 11px 0;
			}
		}

		.item_depth2.active {
			.filter_list.depth3 {
				margin: 0 0 15px;
			}
		}

		.item_depth2.active {
			.filter_list.depth4 {
				margin: 0 0 15px;
			}
		}
	}

	.filter_integrate_box {
		.filter_list {
			.item_depth1 {
				+ .item_depth1 {
					margin-top: 8px;
				}

				&.active + .item_depth1 {
					margin-top: 19px;
				}
			}

			.btn_filter_depth1 {
				height: 50px;
				padding: 0 14px;
				border: 1px solid #000;
				border-radius: 6px;

				&.active {
					&:after {
						content: '선택됨';
						display: inline-block;
						width: 10px;
						height: 7px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/arw_filter_depth1_integrate_active', 'png', 10px, 7px);
						font-size: 0;
					}
				}
			}

			.btn_filter_depth2 {

			}

			&.depth2 {
				margin-left: 14px;
			}
		}

		~ .filter_list_box {
			margin-top: 20px;
		}

		+ .filter_search_box {
			padding-top: 21px;
			border-top: 1px solid variables.$colorGray03;

			.btn_ip_search {
				top: calc(50% + 10px);
			}
		}
	}

	.filter_list_box {
		border-top: 1px solid variables.$colorGray03;

		.filter_list {
			.item_depth1 {
				padding: 20px 0 21px;
				border-bottom: 1px solid variables.$colorGray04;
			}

			.btn_filter_depth1 {
				&:after {
					content: '펼치기';
					display: inline-block;
					width: 9px;
					height: 9px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					font-size: 0;
					@include mixins.ratio_images('../images/ico_filter_depth1', 'png', 9px, 9px);
				}

				&.active {
					font-weight: 700;

					&:after {
						content: '접기';
						@include mixins.ratio_images('../images/ico_filter_depth1_active', 'png', 9px, 1px);
					}
				}

				&.btn_popup {
					&:after {
						content: '팝업 열기';
						width: 5px;
						height: 19px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						transform: rotate(180deg);
						@include mixins.ratio_images('../images/arw_filter_depth3_active', 'png', 5px, 9px);
					}
				}
			}

			.item_depth1.active {

				.filter_list.depth2 {
					margin-left: 7px;
				}
			}
		}
	}

	.filter_search_box.form_search_box {
		margin-top: 20px;

		.form_ip {
			height: 38px;
			border-radius: 19px;
			font-size: variables.$fontSizeXxs;
			line-height: 38px;
		}

		.btn_ip_search {
			width: 20px;
			height: 20px;
			@include mixins.ratio_images('../images/btn_ip_filter_search', 'png', 20px, 20px);
		}
	}

	.selected_filter_box {
		padding: 17px 0 12px;
		border-top: 1px solid variables.$colorGray03;

		~ .showcase_prod_wrap .prod_list {
			border-top-color: variables.$colorGray04;
		}
	}

	.switch_prod_wrap{
		.mark{
			color: variables.$textColorPurple !important;
		}

		.prod_area{
			.prod_info_box{
				.prod_name_group{
					.mark{
						font-weight: variables.$fontWeightBold;
					}
				}

				.prod_desc_info{
					.mark{
						font-weight: variables.$fontWeightMedium;
					}
				}
			}
		}

		&.view_type_list{
			.prod_list{
				.prod_item{
					.prod_order_state{
						margin-top: 53px;
					}

					.prod_btn_wrap{
						.btn_wish_icon{
							width: 26px;
							height: auto;
							margin-bottom: 16px;

							.val{
								display: block;
								margin-top: 2px;
								@include mixins.font_size_min;
								text-align: center;
								color: variables.$textColorLightGray;
							}

							&.active{
								.val {
									color: variables.$textColorGray;
								}
							}
						}
					}

					.prod_related_list{
						margin-top: 16px;

						.btn_item{
							~.btn_item{
								margin-top: 4px;
							}
						}

						.btn_pill{
							font-weight: variables.$fontWeightRegular;

							~ .btn_pill{
								margin-top: 4px;
							}
						}
					}

					&.only_prod{
						.prod_area{
							max-width: none;

							.prod_info_box{
								.prod_summary{
									margin-top: 20px;

									.type{
										display: inline-block;
										height: 20px;
										padding: 0 8px;
										background-color: rgba(variables.$textColorBlack, .06);
										@include mixins.font_size_min;
										font-weight: variables.$fontWeightMedium;
										color: variables.$textColorGray;
										border-radius: 10px;
										vertical-align: top;
									}

									.desc{
										margin-top: 10px;
										@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 3, false);
										@include mixins.font_size_xxs;
										color: variables.$textColorGray;
										white-space: normal;

										.mark{
											font-weight: variables.$fontWeightBold;
										}
									}
								}

								.prod_review{
									margin-top: 16px;

									.review_info{
										@include mixins.justify_width(center, between);

										.user_info{
											font-size: 0;

											.info_item{
												display: inline-block;
												@include mixins.font_size_min;
												color: variables.$textColorLightGray;
												vertical-align: top;
											}

											.gap{
												display: inline-block;
												font-size: 0;
												width: 1px;
												height: 9px;
												background-color: variables.$colorGray03;
												margin: 4px 8px 0;
												vertical-align: top;
												box-sizing: border-box;
											}
										}

										.review_summary_wrap{
											margin-top: 0;
										}
									}

									.desc{
										margin-top: 10px;
										@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 3, false);
										@include mixins.font_size_xxs;
										color: variables.$textColorGray;
										white-space: normal;

										.mark{
											font-weight: variables.$fontWeightBold;
										}
									}
								}
							}
						}
					}
				}
			}
		}

		&.view_type_img {
			.prod_list {
				.prod_item {
					.prod_area{
						.prod_info_box{
							.prod_badge{
								.date{
									display: none;
								}
							}

							.prod_price{
								.point{
									.rate{
										display: none;
									}
								}
							}

							.prod_bottom {
								.btn_wish_icon{
									margin-left: 0;
									width: auto;

									.val{
										display: inline-block;
										margin-left: 4px;
										@include mixins.font_size_min;
										color: variables.$textColorLightGray;
										vertical-align: top;
									}

									&.active{
										.val{
											color: variables.$textColorGray;
										}
									}
								}

								.review_summary_wrap{
									margin-left: 10px;

									.review_klover_box{
										.review_klover_text {
											padding-left: 22px;
											@include mixins.font_size_min;
											font-weight: variables.$fontWeightMedium;

											&:before {
												top: 0;
												width: 18px;
												height: 18px;
												@include mixins.ratio_images('../images/ico_klover_search_thumbnail', 'png', 18px, 18px);
											}
										}
										.review_desc{
											display: none;
										}
									}

									.gap, .review_quotes_text{
										display: none;
									}
								}
							}
						}
					}

				}
			}
		}
	}

	.prod_swiper_wrap{
		&.outside_nav{
			.prod_list{
				.prod_item{
					.prod_area{
						width: 149px;

						.prod_thumb_box{
							.img_box{
								img{
									max-height: calc(149px * 1.618 - 10px);
								}
							}
						}
					}
				}
			}
		}
	}

	&.type_nodata{
		.result_area{
			width: 100%;
			margin-left: 0;

			.prod_swiper_wrap {
				&.outside_nav {
					.prod_list {
						.prod_item {
							.prod_area {
								width: 170px;
							}
						}
					}
				}
			}
		}



	}
}

// 상단 배너영역
