// 연관검색어 영역
.related_keyword_wrap{
	@include mixins.justify_width(center, between);
	margin-bottom: 50px;

	.related_keyword_inner{
		flex: 1;
		font-size: 0;

		.text{
			display: inline-block;
			margin: 7px 13px 0 0;
			@include mixins.font_size_xs;
			font-weight: variables.$fontWeightMedium;
			vertical-align: top;
			color: variables.$textColorLightGray;
		}

		.tag_wrap{
			overflow: hidden;
			display: inline-block;
			max-width: 797px;
			height: 40px;
			vertical-align: top;

			&.active{
				height: auto;

				~.btn_tag_more {
					.ico_tag_more {
						@include mixins.ratio_images('../images/btn_tag_more_active', 'png', 16px, 16px);
					}
				}
			}
		}

		.btn_tag_more{
			display: inline-block;
			width: 36px;
			height: 36px;
			margin-left: 6px;
			border-radius: 100%;
			background-color: variables.$colorGray06;
			vertical-align: top;

			.ico_tag_more {
				display: inline-block;
				width: 16px;
				height: 16px;
				background: {
					repeat: no-repeat;
					position: center;
				}
				@include mixins.ratio_images('../images/btn_tag_more', 'png', 16px, 16px);
				vertical-align: top;
			}
		}
	}

	.hot_keyword_list {
		display: block;
		width: 170px;
		height: 22px;

		.hot_keyword_item {
			display: block;

			@include mixins.clearfix;

			.left_area {
				float: left;
				font-size: 0;
				width: calc(100% - 32px);

				.hot_keyword_link {
					display: inline-block;
					max-width: 100%;

					.rank {
						display: inline-block;
						flex: 0 auto;
						width: 15px;
						margin-right: 10px;
						color: variables.$textColorGray;
						@include mixins.font_size_xxs;
						font-weight: variables.$fontWeightBold;
						vertical-align: top;
					}

					.keyword {
						display: inline-block;
						width: calc(100% - 25px);
						flex: 1;
						color: variables.$textColorBlack;
						@include mixins.font_size_xxs;
						@include mixins.ellipsis(false, true, false);
						vertical-align: top;
					}

					&:hover, &:focus, &:active {
						.keyword {
							text-decoration: underline;
						}
					}
				}
			}

			.right_area {
				float: right;
			}

			& ~ .hot_keyword_item {
				margin-top: 16px;
			}

			&.top {
				.left_area {
					.rank {
						color: variables.$textColorGreen;
					}
				}
			}
		}
	}
}

