/* 스펙비교 레이어 팝업 */
.spec_compare_layer_wrap {
	display: none;
	position: fixed;
	left: 0;
	bottom: -100%;
	z-index: 100;
	width: 100%;
	min-width: 1440px;
	height: 270px;
	box-sizing: border-box;

	.compare_layer_inner {
		@include mixins.inner_width;
		position: relative;
		height: 100%;
		padding: 40px 40px 50px 40px;
		background-color: #fff;
		border: 1px solid #000;
		border-bottom: none;
		border-radius: 20px 20px 0 0;
		box-sizing: border-box;

		@include mixins.clearfix;

		.compare_title_area {
			@include mixins.justify_width(start, start, column);
			width: 326px;
			height: 100%;
			float: left;
			margin-right: 30px;

			.compare_btn_box {
				@include mixins.justify_width;
				flex-shrink: 0;
				min-width: 226px;
				margin-top: auto;

				.btn_ip {
					flex: 1;

					& + .btn_ip {
						margin-left: 6px;
					}
				}
			}
		}

		.compare_prod_area {
			display: block;
			width: calc(100% - 30px - 326px);
			float: left;

			.compare_prod_list {
				display: block;
				font-size: 0;

				.prod_item {
					position: relative;
					display: inline-block;
					width: 118px;
					vertical-align: top;

					& ~ .prod_item {
						margin-left: 30px;
					}

					.prod_area {
						.prod_thumb_box {
							width: 100%;

							.img_box {
								border-radius: 6px;

								&:before {
									content: '';
									position: absolute;
									top: 0;
									left: 0;
									width: 100%;
									height: 100%;
									border: 1px solid variables.$colorGray04;
									border-radius: 6px;
									box-sizing: border-box;
								}
							}
						}
						.prod_info_box {
							margin-top: 12px;

							.prod_name {
								font-size: 14px;
								font-weight: variables.$fontWeightRegular;
							}
						}
					}

					.btn_remove_prod {
						position: absolute;
						right: 0;
						top: 0;
						padding: 0 0 5px 5px;

						.ico_remove {
							display: inline-block;
							width: 22px;
							height: 22px;
							@include mixins.ratio_images('../images/btn_remove_prod', 'png', 8px, 8px);
							background: {
								color: rgba(#000, .6);
								position: center;
								repeat: no-repeat;
							}
							border-radius: 0 6px;
							vertical-align: top;
						}
					}

					// 비교상품 선택 전
					.empty_thumb_box {
						position: relative;
						display: block;
						overflow: hidden;
						width: 100%;
						padding-top: 100%;
						@include mixins.ratio_images('../images/img_spec_compare_empty', 'png', cover);
						background: {
							position: center;
							repeat: no-repeat;
						}
						border-radius: 6px;
						box-sizing: border-box;
					}

					&.empty {
						.prod_area {
							.prod_thumb_box {
								width: 100%;

								.img_box {
									border-radius: 6px;

									&:before {
										content: '';
										position: absolute;
										top: 0;
										left: 0;
										width: 100%;
										height: 100%;
										border: 1px solid variables.$colorGray04;
										border-radius: 6px;
										box-sizing: border-box;
									}
								}
							}
						}
					}
				}
			}
		}

		.btn_layer_close {
			position: absolute;
			top: 26px;
			right: 26px;
			width: 30px;
			height: 30px;
			padding: 3px;
			box-sizing: border-box;

			.ico_layer_close {
				position: relative;
				z-index: 3;
				display: block;
				width: 24px;
				height: 24px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/btn_dialog_close', 'png', 24px, 24px);
			}
		}
	}

	&.open {
		display: block;
		bottom: 0;
	}
}

.input_text_box {
	font-size: 0;

	.form_ip {
		flex: 1;

		&~.form_ip {
			margin-top: 0;
		}
	}

	.gap {
		display: inline-block;
		padding: 0 4px;
		font-size: variables.$fontSizeSm;
		color: variables.$textColorBlack;
		line-height: 42px;
		vertical-align: top;
		text-align: center;
		padding-bottom: 2px;
	}

	.form_sel {
		+.form_sel {
			margin-left: 6px;
		}
	}
}